:root {
  // Primary
  --LIMEGREEN:  hsl(163, 72%, 41%);
  --BRIGHTRED: hsl(356, 69%, 56%);
  --FACEBOOK-COLOR: hsl(208, 92%, 53%);
  --TWITTER-COLOR: hsl(203, 89%, 53%);
  --INSTAGRAM-START: hsl(37, 97%, 70%);
  --INSTAGRAM-MIDDLE: hsl(5, 77%, 71%);
  --INSTAGRAM-END: hsl(329, 70%, 58%);
  --YOUTUBE-COLOR:  hsl(348, 97%, 39%);

  // Replaceable
  --BG-COLOR: hsl(0, 0%, 100%);
  --TEXT-COLOR:hsl(230, 17%, 14%);
  --ALTERNATIVE-COLOR: hsl(228, 12%, 44%);
  --CARD-BG: hsl(227, 47%, 96%);
  --CARD-HOVER: hsl(228, 33%, 91%);
  --TOGGLE-START: hsl(230, 22%, 74%);
  --TOGGLE-END: hsl(230, 22%, 74%);

  // --DARK-TOP-BG: hsl(232, 19%, 15%);
  //  --LIGHT-TOP-BG:  hsl(225, 100%, 98%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --BG-COLOR: hsl(230, 17%, 14%);
    --TEXT-COLOR: hsl(0, 0%, 100%);
    --ALTERNATIVE-COLOR: hsl(228, 34%, 66%);
    --CARD-BG: hsl(228, 28%, 20%);
    --CARD-HOVER: hsl(228, 25%, 27%);
    --TOGGLE-START: hsl(210, 78%, 56%);
    --TOGGLE-END: hsl(146, 68%, 55%);
  }

}

body.light {
  --BG-COLOR: hsl(0, 0%, 100%);
  --TEXT-COLOR:hsl(230, 17%, 14%);
  --ALTERNATIVE-COLOR: hsl(228, 12%, 44%);
  --CARD-BG: hsl(227, 47%, 96%);
  --CARD-HOVER: hsl(228, 33%, 91%);
  --TOGGLE-START: hsl(230, 22%, 74%);
  --TOGGLE-END: hsl(230, 22%, 74%);
}

body.dark {
  --BG-COLOR: hsl(230, 17%, 14%);
  --TEXT-COLOR: hsl(0, 0%, 100%);
  --ALTERNATIVE-COLOR: hsl(228, 34%, 66%);
  --CARD-BG: hsl(228, 28%, 20%);
  --CARD-HOVER: hsl(228, 25%, 27%);
  --TOGGLE-START: hsl(210, 78%, 56%);
  --TOGGLE-END: hsl(146, 68%, 55%);
}